<template src="./page.html"></template>

<script>
    export default {
        name: 'page',
        data(){
            return {
                mode: false,
                pic:'',
                nowChapter: this.$route.params.chapter, //目前頁面章節
                nowPageData:[], //目前頁面內容  
                nowPageApp:'第 1 頁', //顯示畫面
                nowPage:'1',  //實際讀取頁面
                //資料列
                chapterData: [{
                    value: 'chapter1',
                    page:[
                        {"text": "第 1 頁" },
                        {"text": "第 2 頁" },
                        {"text": "第 3 頁" },
                        {"text": "第 4 頁" },
                        {"text": "第 5 頁" },
                        {"text": "第 6 頁" },
                        {"text": "第 7 頁" },
                        {"text": "第 8 頁" }
                    ]
                }, {
                    value: 'chapter2',
                    page:[
                        {"text": "第 1 頁" },
                        {"text": "第 2 頁" },
                        {"text": "第 3 頁" },
                        {"text": "第 4 頁" },
                        {"text": "第 5 頁" },
                        {"text": "第 6 頁" },
                        {"text": "第 7 頁" },
                        {"text": "第 8 頁" },
                        {"text": "第 9 頁" }
                    ]
                }],
            }
        },
        mounted(){
            this.pic = require(`../../static/img/${this.$route.params.chapter}/${this.nowPage}.jpg`);
            this.nowPageData = this.chapterData[parseInt( this.$route.params.chapter.slice(7) ) - 1].page;  //建立資料時,即將選項建出
            this.nowPageApp = '第 1 頁';  //彩現為第 1 頁
        },
        watch:{
            nowPage(){
                this.$nextTick(() => {
                    if(!this.$el) return false  //如果元件還沒出現前,return false
                    let target = this.$el.querySelector("div .active"); //選取active位置
                    if(!target) return false
                    let evr = this.$el.querySelector('.pageSelect');
                    evr.scrollLeft = target.offsetLeft - evr.offsetLeft;
                })
            },
        },
        methods:{
            //連動下拉選單
            selectChange(value){
                let vm = this;
                vm.nowChapter = 'chapter' + parseInt(vm.nowChapter + 1); //維持chapterN格式
                vm.backToPage1(value);
            },
            //下拉及點選圖示跳選頁面
            selectPage(value){
                let vm = this;
                vm.nowPage = value + 1;
                vm.pic = require(`../../static/img/${vm.nowChapter}/${vm.nowPage}.jpg`); //引入頁數
            },
            //點選變更頁面(thumb點選)
            clickPage(event){
                let vm = this;
                vm.nowPage = event.target.dataset.num;
                vm.nowPageApp = `第 ${vm.nowPage} 頁`;
                vm.pic = require(`../../static/img/${vm.nowChapter}/${vm.nowPage}.jpg`); //引入頁數
            },
            //圖片兩旁按鈕
            prev(){
                let vm = this;
                if(vm.nowPage == 1){
                    vm.prevChapter();
                    return
                }
                vm.nowPage --;
                vm.nowPageApp = `第 ${vm.nowPage} 頁`
                vm.pic = require(`../../static/img/${vm.nowChapter}/${vm.nowPage}.jpg`);
            },
            next(){
                let vm = this;
                if(vm.nowPage == Object.keys(vm.nowPageData).length){
                    vm.nextChapter();
                    return
                }
                vm.nowPage ++;
                vm.nowPageApp = `第 ${vm.nowPage} 頁`
                vm.pic = require(`../../static/img/${vm.nowChapter}/${vm.nowPage}.jpg`);
            },
            //上下章按鈕
            prevChapter(){
                let vm = this;
                let preChapterTitle = parseInt( vm.nowChapter.slice(7) ) - 1;
                if(preChapterTitle === 0){
                    alert('已是第一章');
                    return
                }
                vm.nowChapter = 'chapter'+ preChapterTitle;
                vm.backToPage1(preChapterTitle - 1);
            },
            nextChapter(){
                let vm = this;
                let nextChapterTitle = parseInt( vm.nowChapter.slice(7) ) + 1;
                if(nextChapterTitle == parseInt(Object.keys(vm.chapterData).length) + 1){
                    alert('已是最後一章');
                    return
                }
                vm.nowChapter = 'chapter'+ nextChapterTitle;
                vm.backToPage1(nextChapterTitle - 1);
            },
            backToPage1(value){
                let vm = this;
                vm.nowPageData = vm.chapterData[value].page; //定義pageData
                vm.nowPageApp ='第 1 頁';  //彩現頁面定為第一頁
                vm.nowPage ='1';  //記錄為第一頁
                vm.pic = require(`../../static/img/${vm.nowChapter}/${vm.nowPage}.jpg`); //引入頁數
            },
            //載入照片methods
            itemInput(input){
                let vm = this;
                return require(`@/static/img/${vm.nowChapter}/${input + 1}.jpg`);
            },
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" src="./page.scss"></style>
